.page {
  margin: 0;
  padding: 0;
}

.page__content {
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  overflow-x: auto;
}

.logo {
  background-image: url("logo.eddae0ff.png");
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  padding-left: 60px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
}

.nav__list :last-child {
  margin-right: 0;
}

.nav__list :first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  white-space: nowrap;
  letter-spacing: 0;
  text-align: left;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-flex;
  position: relative;
}

.nav__link:hover, .nav__link--is-active {
  color: #00acdc;
}

.nav__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  align-self: flex-end;
  width: 100%;
  height: 4px;
  position: absolute;
}

.nav [data-qa="hover"] {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: -moz-fit-content;
  height: fit-content;
  margin: auto;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__info-area {
  color: #060b35;
  flex-direction: column;
  margin: 40px 0 0;
  padding: 0;
  display: flex;
}

.card__title {
  letter-spacing: 0;
  text-align: left;
  justify-content: left;
  margin: 0 0 4px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__subtitle {
  letter-spacing: 0;
  text-align: left;
  color: #616070;
  justify-content: left;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review-area {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__review-text {
  letter-spacing: 0;
  text-align: right;
  margin: auto;
  font-size: 10px;
  font-weight: 400;
}

.card__price-area {
  letter-spacing: 0;
  text-align: center;
  justify-content: space-between;
  margin-top: 24px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__price-title {
  letter-spacing: 0;
  text-align: left;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  transition: all .3s;
  display: inline;
}

.card__price-value {
  letter-spacing: 0;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: inline;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  letter-spacing: 0;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
}

.card__button:hover {
  color: #34568b;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
  padding: 0;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.content {
  grid-template-columns: repeat(1, 200px);
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px 0;
  display: grid;
}

@media (width >= 488px) {
  .content {
    grid-template-columns: repeat(2, 200px);
    gap: 46px 48px;
  }
}

@media (width >= 768px) {
  .content {
    grid-template-columns: repeat(3, 200px);
    gap: 46px 48px;
  }
}

@media (width >= 1024px) {
  .content {
    grid-template-columns: repeat(4, 200px);
    gap: 46px 48px;
  }
}

